@keyframes rotate-github-loader {
  0% {
    transform: rotate3d(0, 0, 1, 0);
  }

  25% {
    transform: rotate3d(0, 0, 1, 90deg);
  }

  50% {
    transform: rotate3d(0, 0, 1, 180deg);
  }

  75% {
    transform: rotate3d(0, 0, 1, 270deg);
  }

  100% {
    transform: rotate3d(0, 0, 1, 360deg);
  }
}

.react-github-graph-contributions {
  --color-calendar-graph-day-bg: #ebedf0;
  --color-calendar-graph-day-border: #1b1f230f;
  --color-calendar-graph-day-L1-bg: #9be9a8;
  --color-calendar-graph-day-L2-bg: #40c463;
  --color-calendar-graph-day-L3-bg: #30a14e;
  --color-calendar-graph-day-L4-bg: #216e39;
  --color-calendar-graph-day-L4-border: #1b1f230f;
  --color-calendar-graph-day-L3-border: #1b1f230f;
  --color-calendar-graph-day-L2-border: #1b1f230f;
  --color-calendar-graph-day-L1-border: #1b1f230f;
  animation: 1s ease-in-out fadein;
}

@keyframes fadein {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.react-github-graph-contributions svg {
  max-width: 100%;
}

.react-github-graph-contributions svg text {
  font-size: 12px;
}

.react-github-graph-contributions rect:hover {
  opacity: .5;
}

.react-github-graph-contributions .tooltip {
  color: #eee;
  background: #141414e6;
  border-radius: 2px;
  padding: 4px 10px;
  font-size: 12px;
  animation: .3s ease-in-out fadein;
  position: fixed;
  transform: translateY(-100%)translateX(-50%);
}

.react-github-graph-contributions .tooltip:after {
  content: " ";
  height: 0;
  width: 0;
  pointer-events: none;
  background: none;
  border: 5px solid #0000;
  border-top-color: #141414e6;
  margin-left: -5px;
  position: absolute;
  top: 100%;
  left: 50%;
}

.react-github-graph-contributions h2.contributions-header {
  margin-bottom: 0;
}

.react-github-graph-contributions .ContributionCalendar.days-selected .ContributionCalendar-day {
  opacity: .5;
}

.react-github-graph-contributions .ContributionCalendar.days-selected .ContributionCalendar-day.active {
  opacity: 1;
}

.react-github-graph-contributions .ContributionCalendar-label {
  fill: var(--color-text-primary);
  font-size: 9px;
}

.react-github-graph-contributions .ContributionCalendar-day, .react-github-graph-contributions .ContributionCalendar-day[data-level="0"] {
  fill: var(--color-calendar-graph-day-bg);
  shape-rendering: geometricprecision;
  outline: 1px solid var(--color-calendar-graph-day-border);
  outline-offset: -1px;
}

.react-github-graph-contributions .ContributionCalendar-day[data-level="1"] {
  fill: var(--color-calendar-graph-day-L1-bg);
  outline: 1px solid var(--color-calendar-graph-day-L1-border);
}

.react-github-graph-contributions .ContributionCalendar-day[data-level="2"] {
  fill: var(--color-calendar-graph-day-L2-bg);
  outline: 1px solid var(--color-calendar-graph-day-L2-border);
}

.react-github-graph-contributions .ContributionCalendar-day[data-level="3"] {
  fill: var(--color-calendar-graph-day-L3-bg);
  outline: 1px solid var(--color-calendar-graph-day-L3-border);
}

.react-github-graph-contributions .ContributionCalendar-day[data-level="4"] {
  fill: var(--color-calendar-graph-day-L4-bg);
  outline: 1px solid var(--color-calendar-graph-day-L4-border);
}

.react-github-graph-contributions .ContributionCalendar[data-holiday="halloween"] .ContributionCalendar-day[data-level="1"] {
  fill: var(--color-calendar-halloween-graph-day-L1-bg);
}

.react-github-graph-contributions .ContributionCalendar[data-holiday="halloween"] .ContributionCalendar-day[data-level="2"] {
  fill: var(--color-calendar-halloween-graph-day-L2-bg);
}

.react-github-graph-contributions .ContributionCalendar[data-holiday="halloween"] .ContributionCalendar-day[data-level="3"] {
  fill: var(--color-calendar-halloween-graph-day-L3-bg);
}

.react-github-graph-contributions .ContributionCalendar[data-holiday="halloween"] .ContributionCalendar-day[data-level="4"] {
  fill: var(--color-calendar-halloween-graph-day-L4-bg);
}

